.about-container{
    padding-top: 8rem;
}

.about-header{
    font-size: 50px;
}
.about-box{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
@media (max-width: 800px) {
    .about-box{
      flex-direction: column;
    }
  }
.about-text{
    font-size: 20px;

}

.img img {
    margin: auto;
    border-radius: 10px;
}
.img{
    
    padding-top:4rem;
    border-radius: 10%;
}
.skills{
    font-size: 13px;
    list-style: circle;
    margin-top:2rem;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 20px;
}